







import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
@Component
export default class TitleManage extends Vue {
  typeNew = this.$route.query.typeNew;
  @Prop(String) type!: string;

  get title() {
    const hash: { [key: string]: boolean } = {
      '编辑支出': this.type === "-",
      '编辑收入': this.type === "+",
      '新增支出': this.typeNew === "-",
      '新增收入': this.typeNew === "+",
    };
    for (let i in hash) {
      if (hash[i]) {
        return i;
      }
    }
    return "";
  }
  saveChanges() {
    this.$emit("submitChanges", this.typeNew);
  }
}
